.table-responsive{
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}
.table{
  border-collapse: collapse;
  width: 100%;
  @include e(head){
    border-bottom: 1px solid getColor("grey.borderGray");
    @include e(col){
      @include getFontSize("body14");
      white-space: pre;
      font-weight: 400;
      text-align: left;
      padding: {
        top: getSpacing("6x");
        left: getSpacing("6x");
        right: getSpacing("6x");
        bottom: getSpacing("2x");
      }
    }
  }
  @include e(body){
    @include e(col){
      @include getFontSize("body16");
      font-weight: 400;
      text-align: left;
      padding: getSpacing("4x") getSpacing("6x");
      white-space: pre;
      @include m(fw){
        max-width: 170px;
        box-sizing: content-box;
      }
    }
  }
}